import faq from "./assets/faq";
import pureSlider from "./assets/slider";
import amount from './assets/amount';
import certificate from "./assets/certificate";
import range from "./assets/range";
import dropdown from "./assets/dropdown";
import calc from './assets/calc';
import appendChildSlider from "./assets/appenChildSlider";
import fixedScroll from "./assets/fixedScroll";
import cabinetScript from "./assets/cabinetScript";
import validation from "./assets/validation";
import filtersOpen from "./assets/filtersOpen";
import mobileMenu from "./assets/mobileMenu";
import backBtn from "./assets/backBtn";
import seoMore from "./assets/seoMore";

document.addEventListener('DOMContentLoaded', () => {
  // pureSlider('.big-slider__slides', 'dots', 'classic', false, false, false, 5000);
  appendChildSlider({
    sliderSelector: '.big-slider__wrapper',
    btnLeftSelector: '.left',
    btnRightSelector: '.right',
    itemSelector: '.big-slider__slide',
    transitionTime: '.9',
    dots: '.dots',
    autoScroll: 6000
    });
    // var i = 'dssdadas';
    // i.split();
    // console.log(i.split())
  pureSlider('.product-imgs__block', 'imgs', 'opacity');
  faq();
  amount();
  certificate();
  range();  
  dropdown();
  const sizesData = {
    chest: {
      'AA': [10, 11],
      'A': [12, 13],
      'B': [14, 15],
      'C': [16, 17],
      'D': [18, 19],
      'E': [20, 21],
      'F': [22, 23],
      'G': [24, 25],
      'H': [26, 27],
      'I': [28, 29]
    },
    hip: {
      'XS': [86, 90],
      'S': [91, 94],
      'M': [95, 98],
      'L': [99, 106],
      'XL': [107, 114],
      'XXL': [115, 122],
      '3XL': [123, 130],
      '4XL': [131, 138]
    }

  }

  if(document.documentElement.clientWidth < 768) {
    const slider  = document.querySelector('.extra__slider');
    const slider2 = document.querySelector('.similar__slider');

    if(slider) {
      slider.classList.remove('extra__slider');
      slider.classList.add('extra__slider-mobile');
    }
    if(slider2) {
      slider2.classList.remove('similar__slider');
      slider2.classList.add('extra__slider-mobile');
    }
  }
  window.addEventListener('resize', () => {
    if(document.documentElement.clientWidth < 768) {
      const slider  = document.querySelector('.extra__slider');
      const slider2 = document.querySelector('.similar__slider');

      if(slider2) {
        slider2.classList.remove('similar__slider');
        slider2.classList.add('extra__slider-mobile');
      }
      if(slider) {
        slider.classList.remove('extra__slider');
        slider.classList.add('extra__slider-mobile');
      }
    }
  })
  const forgotLink = document.querySelector('.forgot');
  if(forgotLink) {
    forgotLink.addEventListener('click', (e) => {
      e.preventDefault();
      const loginForm = document.querySelector('.login__login');
      loginForm.style.display = 'none';
      loginForm.nextElementSibling.style.display = 'block';
      const loginFirst = document.querySelector('.login__first');
      loginFirst.style.display = 'none';
      loginFirst.nextElementSibling.style.display = 'block';
    })
  }

  calc(sizesData);
  appendChildSlider({
    sliderSelector: '.extra__slider',
    btnLeftSelector: '.left',
    btnRightSelector: '.right',
    itemSelector: '.item',
    transitionTime: '.5',
    displayedSlides: 4

  });
  appendChildSlider({
    sliderSelector: '.similar__slider',
    amount: 2,
    btnLeftSelector: '.left',
    btnRightSelector: '.right',
    itemSelector: '.item',
    transitionTime: '.5',
    displayedSlides: 4
  });
  fixedScroll('.account__menu', 120 , 270);
  fixedScroll('.product-imgs', 20, 240);


  cabinetScript();
  validation();
  filtersOpen();
  mobileMenu();
  backBtn();
  seoMore();
})

