const appendChildSlider = (
    {
        sliderSelector, 
        amount = 1,
        btnLeftSelector, 
        btnRightSelector,
        itemSelector, 
        transitionTime, 
        dots = false, 
        autoScroll = false, 
        displayedSlides = 1,
        mobileAmount = 1
    }) => {
    const slider = document.querySelector(sliderSelector);
    if(slider) {
        var items = slider.querySelectorAll(itemSelector);
        const dotsBtns = document.querySelector(dots);
        if(items.length <= 2) {
            const newItem = items[0].cloneNode(true);
            slider.children[0].appendChild(newItem);
            var items = slider.querySelectorAll(itemSelector)
        } else {
            var items = slider.querySelectorAll(itemSelector);   
        }

        function initSlider(amount, items) {
            for(let i =0; i < amount; i++) {
                slider.children[0].insertAdjacentElement('afterbegin', slider.children[0].children[slider.children[0].children.length - 1]);
                slider.children[0].style.transform = `translateX(-${(items[0].clientWidth + (+getComputedStyle(items[0]).marginRight.slice(0, -2) + +getComputedStyle(items[0]).marginLeft.slice(0, -2)))*amount}px)`
            }
            // if(!((items.length % 2) === 0) && (displayedSlides === 1)) {
            //     // slider.children[0].style.transform = `translateX(${items[0].clientWidth/2 + (+getComputedStyle(items[0]).marginRight.slice(0, -2) + +getComputedStyle(items[0]).marginLeft.slice(0, -2))/2}px)`
              
            // } else if (!(displayedSlides % 2 === 0) ) {
            //     if(((items.length % 2) === 0)) {
            //         slider.children[0].style.transform = `translateX(${items[0].clientWidth/2}px)`
            //     }
            // }
       
            
            if(dotsBtns) {
                for(let i = 0; i < items.length; i++) {
                    items[i].setAttribute('item-slide', i + 1);
                }
                for(let i = 0; i < slider.children[0].children.length; i++) {
                    const dot = document.createElement('div');
                    dot.setAttribute('class', 'dot');
                    dot.setAttribute('data-slide-to', i + 1);
                    dotsBtns.appendChild(dot);
                    
                }
                const dots = slider.querySelectorAll('.dot');
                // console.log(dots.length/2)
                    dots[0].classList.add('dot_active')
                function addListener() {
                    const dots = slider.querySelectorAll('.dot')
                    dots.forEach(dot => {
                        dot.addEventListener('click', (e) => {
                            const activeDot = document.querySelector('.dot_active');
                            const slideFrom = activeDot.getAttribute('data-slide-to');
                            const slideTo = e.target.getAttribute('data-slide-to');
                            // console.log(slideTo - slideFrom)
                            dots.forEach(dot => dot.style.pointerEvents = 'none')
                            function addPointerEvents() {
                                dots.forEach(dot => dot.style.pointerEvents = 'unset')
                            }
                            setTimeout(addPointerEvents, transitionTime * 1000)
                            if(activeDot.getAttribute('data-slide-to') == 1 && e.target.getAttribute('data-slide-to') == items.length) {
                                scrollLeft(1)
                                
                                dots.forEach(dot => dot.classList.remove('dot_active'))
                                    e.target.classList.add('dot_active')
                            } else if (activeDot.getAttribute('data-slide-to') == items.length && e.target.getAttribute('data-slide-to') == 1) {
                                scrollRight(1)
                                dots.forEach(dot => dot.classList.remove('dot_active'))
                                    e.target.classList.add('dot_active')
                            } else {
                                if((slideTo - slideFrom) > 0) {
                                    if(Math.abs(slideTo - slideFrom) > Math.floor(items.length/2)) {
                                        scrollLeft(+items.length - Math.abs(slideTo - slideFrom));
        
                                    } else {
                                        scrollRight(slideTo - slideFrom);
        
                                    }
                                    dots.forEach(dot => dot.classList.remove('dot_active'))
                                    e.target.classList.add('dot_active')
                                } else {
                                    
                                    if(Math.abs(slideTo - slideFrom) >= (items.length/2).toFixed(0)) {
                                        scrollRight(+items.length - Math.abs(slideTo - slideFrom));
        
                                    } else {
                                        scrollLeft(Math.abs(slideTo - slideFrom));
        
                                    }
                                    dots.forEach(dot => dot.classList.remove('dot_active'))
                                    e.target.classList.add('dot_active')
                                }
                            }
                            
                            
                        });
                    }); 
                }
                addListener()
                // window.addEventListener('resize', () => {
                //     addListener()
    
                // })   
            }
            const btnLeft = slider.parentElement.querySelector(btnLeftSelector),
                  sliderItems = (slider.clientWidth / items[0].clientWidth).toFixed(0),
                  btnRight = slider.parentElement.querySelector(btnRightSelector);
            if(amount > sliderItems) {
                amount = sliderItems;
            }
            var paused = false;
            function activateAnimation() {
                paused = setInterval(function() {
                    scrollRight(amount);
                }, autoScroll);
            }
            if(autoScroll) {
                
                activateAnimation();
                slider.addEventListener('mouseenter', () => {
                    clearInterval(paused);
                })
            
                slider.addEventListener('mouseleave', () => {
                    clearInterval(paused);

                    activateAnimation();
                })
            }
    
            function scrollRight(amount) {
                items.forEach(item => {
                    item.style.transition = '0s';
                    item.classList.add('scrolling');
                    btnRight.style.pointerEvents = 'all';
    
                })
                slider.style.pointerEvents = 'none';

                items.forEach(item => {
                    item.style.transition = `${transitionTime}s all`;
                    item.style.transform = `translateX(-${(item.clientWidth + +getComputedStyle(item).marginRight.slice(0, -2) + +getComputedStyle(item).marginLeft.slice(0, -2)) * amount}px)`;
                    btnRight.style.pointerEvents = 'none';
                })
                for(let i = 0; i < amount; i++) {
                    
                    function toLeft() {
                        items.forEach(item => {
                            item.classList.remove('scrolling');
                            item.style.transition = '0s';
    
                            item.style.transform = 'none';                    btnRight.style.pointerEvents = 'all';

                        })
                        slider.style.pointerEvents = 'all';

                        slider.children[0].appendChild(slider.children[0].children[0]);
                    }
                    setTimeout(toLeft, transitionTime * 1000)
                }
                if(dotsBtns) {
                    const dots = document.querySelectorAll('.dot');
                    const activeDot = document.querySelector('.dot_active');
                    
                    dots.forEach(dot => {
                        dot.classList.remove('dot_active')
                        if(activeDot.getAttribute('data-slide-to') == items.length) {
                            dots[0].classList.add('dot_active')
                        } else {
                            if(dot.getAttribute('data-slide-to') == (+activeDot.getAttribute('data-slide-to') + 1)) {
                                dot.classList.add('dot_active')
                            }
                        }
                        
                    })
                    
                }
            }
            function scrollLeft(amount) {
                items.forEach(item => {
                    // item.style.transform = 'none';
                    item.style.transition = '0s all';
                    item.classList.add('scrolling');
    
                    btnLeft.style.pointerEvents = 'all';
    
                })
                items.forEach(item => {
                    item.style.transition = `${transitionTime}s all`;
    
                    item.style.transform = `translateX(${(item.clientWidth + +getComputedStyle(item).marginRight.slice(0, -2) + +getComputedStyle(item).marginLeft.slice(0, -2)) * amount}px)`;
                    btnLeft.style.pointerEvents = 'none';
                })
                for(let i = 0; i < amount; i++) {
                    function toRight() {
                        items.forEach(item => {
                            item.style.transition = '0s all';
                            item.classList.remove('scrolling');

                            item.style.transform = 'none';
                            btnLeft.style.pointerEvents = 'all';
        
                        })
                        slider.children[0].insertAdjacentElement('afterbegin', slider.children[0].children[slider.children[0].children.length - 1]);
                    }
                    setTimeout(toRight, transitionTime * 1000)
    
                    
                }
                if(dotsBtns) {
                    const dots = document.querySelectorAll('.dot');
                    const activeDot = document.querySelector('.dot_active');
                    
                    dots.forEach(dot => {
                        dot.classList.remove('dot_active')
                        if(activeDot.getAttribute('data-slide-to') == 1) {
                            dots[items.length - 1].classList.add('dot_active');
                        } else {
                            if(dot.getAttribute('data-slide-to') == activeDot.getAttribute('data-slide-to') - 1) {
                                dot.classList.add('dot_active')
                            }
                        }
                        
                    })
                    
                }
            }
            if(btnLeft) {
                btnRight.addEventListener('click', () => {
                    scrollRight(amount);
                    
                })
                btnLeft.addEventListener('click', () => {
                    scrollLeft(amount);
                    
                })
    
            }
            function currentSlide() {
                items.forEach(item => {
                    if(!item.classList.contains('scrolling')) {
                        item.style.transform = `translateX(0px)`;
                        item.style.transition = `${transitionTime}s all`
                    }
                    
                })
            }
            let viewport = slider.children[0].clientWidth,
                viewSlide = 0,
                posx = 0;
            function mouseDown(e) {
                if(autoScroll) {
                    clearInterval(paused);
                }
                if(e.identifier === 0) {
                    items.forEach(item => {
                        item.style.transition = '0s all';        
                    })
                    posx = e.clientX;
                    return posx
                } else {
                    e.preventDefault();
                    posx = e.clientX;
                    items.forEach(item => {
                        item.style.transition = '0s all';        
                    })
                    return posx
                }
                
            }
    
            function mouseUp(e) {
                if (e.clientX - posx < - viewport * 0.3 * amount) {
                    slider.style.transition = `${transitionTime}s all`;
                    scrollRight(amount);
                } else {
                    slider.style.transition = `${transitionTime}s all`;
                    currentSlide();
                }
                posx = 0;
                if(autoScroll) {
                    clearInterval(paused);
                    activateAnimation();
                }
            }
    
            function moving(e) {
                if(autoScroll) {
                    clearInterval(paused);
                }
            function leaving() {
                items.forEach(item => {
                    item.style.transition = `${transitionTime}s all`;        
                })
                currentSlide();
                posx = 0;
            }
            slider.addEventListener('mouseleave', () => {
                leaving();
            })
            if (posx > 0) {
                items.forEach(item => {
                    item.style.transition = `0s all`;        
                })
                items.forEach(item => {
                    item.style.transform = `translateX(${-viewSlide * viewport -posx + e.clientX}px`;
                })
                
                if (-posx + e.clientX < - viewport * 0.3) {
                    items.forEach(item => {
                        item.style.transition = `${transitionTime}s all`;        
                    })
                    scrollRight(amount);
                    if(autoScroll) {
                        clearInterval(paused);
                        activateAnimation();
                    }
                    posx = 0;
                } else if (e.clientX - posx > viewport * 0.3) {
                    items.forEach(item => {
                        item.style.transition = `${transitionTime}s all`;        
                    })
                    scrollLeft(amount);
                    if(autoScroll) {
                        clearInterval(paused);

                        activateAnimation();
                    }
                    posx = 0;
                }                
            } else if (posx == 0) {
                slider.removeEventListener('mousemove', moving);
                slider.removeEventListener('touchmove', moving);
            } 
            }  
    
            slider.addEventListener('touchstart', (e) =>{
                posx = e.touches[0].clientX;
                items.forEach(item => {
                    item.style.transition = `${transitionTime}s all`;        
                })
                return posx
            }, {passive: true})
            slider.addEventListener('touchmove', e =>{
                moving(e.touches[0]);
            }, {passive: true})
            slider.addEventListener('touchend', e => {
                mouseUp(e);
            })
    
            slider.addEventListener(`mousedown`, (e) => {
                mouseDown(e);
            })
            slider.addEventListener('mouseup', (e) => {
                mouseUp(e);
            })
            slider.addEventListener('mousemove', (e) => {
                moving(e);
            })
            
         
        }
        if(document.documentElement.clientWidth < 768) {
            if(mobileAmount != amount && mobileAmount) {
                initSlider(mobileAmount);
            } else {
                initSlider(amount, items);
            }
        } else{
            initSlider(amount, items);
        }
        
    }
}

export default appendChildSlider;
