const calc = (sizes) => {
    const calcForm = document.querySelector('.guide__form');
    if(calcForm) {
        const chest = calcForm.querySelector('.chest'),
              underChest = calcForm.querySelector('.under-chest'),
              hip = calcForm.querySelector('.hip'),
              insertPlace = document.querySelector('.guide__results'),
              input = calcForm.querySelectorAll('input'),
              error = calcForm.querySelector('.guide__error');
        input.forEach(i => {
            i.addEventListener('focus', () => {
                i.classList.remove('wrong')
            })
            i.addEventListener('input', () => {
                i.value = i.value.replace(/^([^0-9]*)$/g, '') 
            })
        })
        calcForm.addEventListener('submit', (e) => {
            e.preventDefault();
            error.classList.remove('guide__error_visible')

            // console.log(insertPlace.children[0])
            for(let key in  sizes.chest) {
                if(Math.round(+chest.value - +underChest.value) >= sizes.chest[key][0] && Math.round(+chest.value - +underChest.value) <= sizes.chest[key][1]) {
                    insertPlace.style.padding = '16px 21px 24px 16px';

                    insertPlace.children[0].textContent = `Размер лифа LoveLace — ${key}`
                    // console.log(key)
                }
                
            }

            if((Math.round(+chest.value - +underChest.value) < Object.values(sizes.chest)[0][0]) || Math.round(+chest.value - +underChest.value) > Object.values(sizes.chest)[Object.keys(sizes.chest).length - 1][1] || !(+chest.value) || !(+underChest.value)) {
                chest.classList.add('wrong');
                underChest.classList.add('wrong');
                error.classList.add('guide__error_visible')
                
            }
                

            
            for(let key in  sizes.hip) {
                if(Math.round(+hip.value) >= sizes.hip[key][0] && Math.round(+hip.value) <= sizes.hip[key][1]) {
                    insertPlace.style.padding = '16px 21px 24px 16px';
                    insertPlace.children[1].style.marginTop = '16px';
                    insertPlace.children[1].textContent = `Размер трусиков — ${key}`
                } 

            }
            if((Math.round(+hip.value) < Object.values(sizes.hip)[0][0]) || Math.round(+hip.value) > Object.values(sizes.hip)[Object.keys(sizes.hip).length - 1][1]) {
                hip.classList.add('wrong');
                error.classList.add('guide__error_visible')
            }
           
        })
    }
}

export default calc;