const faq = () => {
    const faqBtn = document.querySelectorAll('.faq__title')
    if(faqBtn[0]) {
        const product = document.querySelector('.product-imgs');
        faqBtn.forEach(item => {
            
            item.addEventListener('click', () => {
                const rect = item.parentElement.clientHeight;

                const items = document.querySelectorAll('.faq-item')
                if(item.parentElement.classList.contains('faq-item_active')) {
                    item.parentElement.classList.remove('faq-item_active');
                    // console.log()
                    if(document.documentElement.clientHeight / 2 + 100 > item.getBoundingClientRect().top) {

                        if(product) {
                            function positionAbsol() {
                                product.style.cssText = `
                                position: absolute;
                                bottom: 0px;
                                top: unset;
                            `
                            }
                            positionAbsol();
                            // setTimeout(positionAbsol, 10)
                
                        }
                    }
                    

                } else {
                    if(!item.parentElement.classList.contains('faq-filter') && !product) {
                        items.forEach(i => i.classList.remove('faq-item_active'))
                        // console.log(item.parentElement.parentElement)
                        // window.scrollBy(0, -rect)
                    }
                    item.parentElement.classList.add('faq-item_active');

                }
                
            })
        })

    }
}

export default faq;