const dropdown = () => {
    const dropdown = document.querySelectorAll('.dropdown__btn');
    if(dropdown[0]) {
      document.addEventListener('click', function (e) {
        if (!e.target.classList.contains('dropdown__btn') && !e.target.classList.contains('dropdown__menu') && !e.target.parentElement.classList.contains('dropdown__btn') && !e.target.parentElement.parentElement.classList.contains('dropdown__btn')) {
          dropdown.forEach(function (btn) {
            btn.nextElementSibling.classList.remove('show');
            btn.parentElement.classList.remove('rotate');
          });
        }
      });
      dropdown.forEach(function (btn) {
        btn.parentElement.addEventListener('click', function () {
    
          if(btn.parentElement.classList.contains('rotate')) {
            btn.nextElementSibling.classList.remove('show');
            btn.parentElement.classList.remove('rotate');
          } else {
            dropdown.forEach(function (btn) {
              btn.nextElementSibling.classList.remove('show');
              btn.parentElement.classList.remove('rotate');
            });
            btn.nextElementSibling.classList.add('show');
            btn.parentElement.classList.add('rotate');
    
          }
    
        });
      });
    }
    
}

export default dropdown;