const mobileMenu = () => {
    const hamburger = document.querySelector('.hamburger');
    if(hamburger) {
        const mobileMenu = document.querySelector('.header-mobile');
        hamburger.addEventListener('click', (e) => {
            e.preventDefault();
            if(mobileMenu.classList.contains('header-mobile__active')) {
                document.body.style.overflow = 'visible';
            } else {
                document.body.style.overflow = 'hidden';
            }
            hamburger.classList.toggle('hamburger_active');
            mobileMenu.classList.toggle('header-mobile__active');

        })
        document.addEventListener('click', (e) => {
            function isDescendant(parent, child) {
                var node = child.parentNode;
                while (node != null) {
                    if (node == parent) {
                        return false;
                    }
                    node = node.parentNode;
                }
                return true;
            }
            if(isDescendant(mobileMenu, e.target) && !e.target.classList.contains('hamburger') && !e.target.classList.contains('header-mobile') && !e.target.parentElement.classList.contains('hamburger')) {
                hamburger.classList.remove('hamburger_active');
                mobileMenu.classList.remove('header-mobile__active');
                document.body.style.overflow = 'visible';

            }
           
        })
        const itemsHasChild = mobileMenu.querySelectorAll('.has-child');
        itemsHasChild.forEach(item => {
            item.addEventListener('click', () =>{
                item.classList.toggle('has-child_active')
            })
        })
    }
}

export default mobileMenu;