const filtersOpen = () => {
    const filtersOpenBtn = document.querySelector('.catalog-open-filters');
    if(filtersOpenBtn){
        filtersOpenBtn.addEventListener('click', () => {
            const filtersMenu = document.querySelector('.catalog-filters');
            if(filtersMenu.classList.contains('active')) {
                filtersMenu.style.display = 'none';
                filtersMenu.classList.remove('active')
            } else {
                filtersMenu.classList.add('active')
                filtersMenu.style.display = 'block';

            }
        })
    }
    
}

export default filtersOpen;